import * as React from 'react';
import { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
    Grid, Button,
    
} from '@material-ui/core';

import {PDFReader} from './PDFReader';

import {
    faChevronLeft,
    faChevronRight
} from '@fortawesome/free-solid-svg-icons';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

interface ImageViewerProps {
    images: any[];
    index: number;
    onClose: () => void;
}

const useStyles = makeStyles({
	root: {
        flexWrap:'wrap',
        position: 'fixed',
        width: '100%',
        height: '100%',
        zIndex: 9999,
        right: '0px',
        bottom: '0px',
        top: '0px',
        left: '0px',
        textAlign: "center",
        backgroundColor: 'rgba(0, 0, 0, 0.5)'
    },
    container:{
        height: '100%',
        outline: 0,
    },
    image: {
        width: '80%',
        height: '70%',
        backgroundColor: '#ff1hff',
        maxHeight: '500px',
        objectFit: 'contain',
    },
    previousButton: {

    },
    nextButton: {
        
    },
    pdf: {
        height: '70%',
        backgroundColor: '#ff1hff',
        objectFit: 'contain',
        overflowY: 'scroll'
    },
    closeButton: {
        textTransform: "inherit", 
        float: 'right', 
        marginRight: '10px',
        marginTop: '10px'
    }
});

export const ImageViewer: React.FC<ImageViewerProps> = (props: ImageViewerProps): JSX.Element => {
    // const buttonclasses = ButtonStyle();

    const {
        images,
        index
    } = props;

    const classes = useStyles();
    const [currentIndex, setCurrentIndex] = useState(index);
    const [currentImage, setCurrentImage] = useState(images[index]);
    // const [buttonClicked, setButtonClicked] = useState(false);
    // const currentImage = images[1];
    let num = 0;

    console.log(images, index, currentIndex);

    useEffect(()=>{
        setCurrentIndex(index)
        setCurrentImage(images[index])
        console.log('imageviewer index',index);
    },[props.index])

    useEffect(()=>{
        setCurrentImage(images[currentIndex])
        console.log('imageviewer currentindex',currentIndex);
    },[currentIndex])

    function handlePrevious(){
        // setButtonClicked(true);
        if(num !== 0) num=num-1;
        if(currentIndex !== 0) {
            setCurrentIndex(currentIndex-1);
            setCurrentImage(images[currentIndex-1]);
        };
        // setButtonClicked(false);
        console.log('handlePrevious',currentIndex, num);
    }

    function handleNext(){
        // setButtonClicked(true);
        if(num !== images.length) num=num+1;
        if(currentIndex !== images.length) {
            setCurrentIndex(currentIndex+1);
            setCurrentImage(images[currentIndex+1]);
        };
        // setButtonClicked(false);
        console.log('handleNext',currentIndex, num);
    }

    return (
        <div className={classes.root}>
            <Grid container direction="row" justify="space-between" alignItems="center" className={classes.container} >
                <Grid item xs={1}>
                    <Button 
                        disabled={currentIndex === 0 ? true: false}
                        className={classes.previousButton}
                        onClick={handlePrevious}
                        style={{textTransform: "inherit"}}
                    >
                        <FontAwesomeIcon 
                            icon={faChevronLeft}
                            className="fa-4x"
                            style={{ 
                                color: currentIndex === 0 ? 'rgba(0.5, 0, 0.5, 0.5)':'#FFFFFF',
                                marginRight: '8px'
                            }} />
                    </Button>
                </Grid>
                <Grid item xs={10} style={{overflowY: 'scroll'}}>
                    {
                        (currentImage.type !== 'pdf') ? (
                            <img src={currentImage.src} className={classes.image} onClick={props.onClose}/>
                        ) : (
                            <div className={classes.pdf} onClick={props.onClose}>
                                <PDFReader url={currentImage.src} showAllPage={true} height={500}/>
                            </div>
                        )
                    }
                </Grid>
                <Grid item xs={1}>
                    <Button 
                        disabled={currentIndex === (images.length -1) ? true: false}
                        className={classes.nextButton}
                        onClick={handleNext}
                        style={{textTransform: "inherit"}}
                    >
                        <FontAwesomeIcon 
                            icon={faChevronRight}
                            className="fa-4x"
                            style={{ 
                                color: currentIndex === (images.length -1) ? 'rgba(0.5, 0, 0.5, 0.5)':'#FFFFFF',
                                marginRight: '8px'
                            }} />
                    </Button>
                </Grid>
            </Grid>
        </div>
    )
}

ImageViewer.defaultProps = {}
