import * as React from 'react';
import { Grid, Typography, Stepper, Step,
    StepLabel, StepConnector, Breadcrumbs, Link} from '@material-ui/core/';
import { makeStyles, withStyles } from '@material-ui/core/styles';

interface PageHeaderComponentProps {
    label: string;
    steps: any[];
    activeStep: number;
    id:string;
}

const headerStyle = makeStyles({
    headerLabel: {
        paddingTop: '20px'
    },
    container: {
        backgroundColor: '#FFFFFF',
        paddingLeft: '5px',
        paddingTop: '10px'
    },
    title: {
        fontSize: '1.5rem',
        alignItems: 'flex-start',
        fontWeight: 600
    },
    label:{
        fontWeight: 600
    },
    breadCrumbs: {
        marginBottom: '10px',
    },
    headerTitle: {
        fontSize: 32,
        fontWeight: 700,
    },
    linkPrevious: {
        fontSize: 12,
        fontWeight: 300,
        color: 'black',
        cursor: 'pointer',
    },
    linkCurrent: {
        fontSize: 12,
        color: 'black',
        cursor: 'pointer',
    },
});

const StepConnectorStyle = withStyles({
    alternativeLabel: {
        top: 10,
        left: 'calc(-50% + 12px)',
        right: 'calc(50% + 12px)',
    },
    active: {
      '& $line': {
        borderColor: '#3AB77D',
      },
    },
    completed: {
      '& $line': {
        borderColor: '#3AB77D',
      },
    },
    line: {
      borderColor: '#eaeaf0',
      borderTopWidth: 3,
      borderRadius: 1,
    },
})(StepConnector);

export const PageHeaderComponent: React.FC<PageHeaderComponentProps> = (
	props: PageHeaderComponentProps,
): JSX.Element => {
    const {
        steps,
        activeStep
    } = props;
    const classes = headerStyle()
    return (
        <Grid container justify="flex-start" alignItems="flex-start" className={classes.container}>
            <Grid item xs={6}>
                <Breadcrumbs className={classes.breadCrumbs}>
                    <Link className={classes.linkPrevious}>PROPOSALS</Link>
                    <Link className={classes.linkCurrent}>{props.label}</Link>
                </Breadcrumbs>
                <Typography className={classes.headerTitle}>{props.label}</Typography>
            </Grid>
            { steps.length > 0 ?
                <Grid item xs={6}>
                    <Stepper id={props.id} alternativeLabel activeStep={activeStep} connector={<StepConnectorStyle />}>
                        {steps.map(data => (
                            <Step key={data}>
                                <StepLabel className={classes.label}>{data}</StepLabel>
                            </Step>
                        ))}
                    </Stepper>
                </Grid>: null
            }
        </Grid>
    )
}

PageHeaderComponent.defaultProps = {
    label: "Page",
    id: "stepper_1",
    steps: [],
    activeStep: 0
}