export const FinalizeProposalPage = (props: IFinalizeProposalPage.IProps) => {
	console.log('props: ',props);
    const classes = viewClientStyles();
    const buttonclasses = ButtonStyle();
    const { contractNode, clientData, SetBenefitNodes, SetClientData, exclusionList, SetExclusionList } = props;

	let isApprover = false;


	if(props.match.params.user_type === 'uploader') isApprover = false;
	if(props.match.params.user_type === 'approver') isApprover = true;

	const [redirectToDashboard, setRedirectToDashboard] = useState(false);
	const [uploaded, setUploaded] = useState<any[]>([]);
    const [selectedFile, setSelectedFile] = useState("");
    const [currentStep, setCurrentStep] = useState(0);
    const [loading, setLoading] = useState(false);
    const [clientLoaded, setClientLoaded] = useState(false);
    const [nodesLoaded, setNodesLoaded] = useState(false);
    const [contractLoaded, setContractLoaded] = useState(false);
    const [closeAccountOpen, setCloseAccountOpen] = useState(false);
    const [confirmCloseAccount, setConfirmCloseAccount] = useState(false);
    const [closeAccountMessage, setCloseAccountMessage] = useState<any>(null);
    const [closeAccountTitle, setCloseAccountTitle] = useState("");
    const [snackbarOpen, setSnackbarOpen] = useState(false);
	const [snackbarMsg, setSnackbarMsg] = useState<any>(null);
    const [invalidFields, setInvalidFields] = useState<string[]>([]);
	const [wrongFormatFields, setWrongFormatFields] = useState<string[]>([]);
	const [wrongFormatFieldIds, setWrongFormatFieldIds] = useState<string[]>([]);
	const steps = ['Review Details', 'Review Benefits', 'Finalize'];

 	console.log('uploaded',JSON.stringify(uploaded));

    const renderStep = (step) => {
        switch(step) {
            case 0:
                return <Details 
                    viewOnly 
                    kycEditable 
                    invalidFields={invalidFields}
					wrongFormatFields={wrongFormatFields}
					wrongFormatFieldIds={wrongFormatFieldIds}
					setWrongFormatFieldIds={setWrongFormatFieldIds}
                    classes={classes} 
                    {...props} />;
            case 1:
                return <StandardBenefitPlan 
                            viewOnly 
                            classes={classes} 
                            {...props}
                            SetNewNodeCounter={() => console.log("SetNewNodeCounter")}
                            newNodeCounter={0}  
                        />;
            case 2:
                return <Finalize 
                            isApprover={isApprover}
                            classes={classes} 
                            selectedFile={selectedFile}
                            setDownloadFile={handleDownloadFile}
                            setSelectedFile={setSelectedFile}
                            setDeleteFile={handleDeleteFile}
                            uploaded={uploaded}
                            setFileUpload={handleFileUpload}
                        />;
            default:
              return null;
        }
    }

    const setStepBack = () => {
        if (currentStep > 0) {
            setCurrentStep(currentStep - 1);
        }
    }

    const setStepForward = () => {
        if(currentStep === 0){
            const error_fields = getInvalidFields(clientData);
            if (error_fields["fields"].length > 0) {
                setInvalidFields(error_fields["fields"]);
                if(error_fields["wrong_format"].length > 0){
                    setWrongFormatFields(error_fields["wrong_format"]);
                    setWrongFormatFieldIds(error_fields["wrong_format_ids"]);
                    setSnackbarMsg(
                        <div>
                            <div>
                                {'Please fill out the following fields first: ' + error_fields["fields"].join(', ')}
                            </div>
                            <div style={{paddingTop: '10px'}}>
                                {'Please correct the email format: ' + error_fields["wrong_format"].join(', ')}
                            </div>
                        </div>
                    );
                }else{
                    setSnackbarMsg('Please fill out the following fields first: ' + error_fields["fields"].join(', '));
                }
                setSnackbarOpen(true);
            }else if(error_fields["wrong_format"].length > 0){
                setWrongFormatFields(error_fields["wrong_format"]);
                setWrongFormatFieldIds(error_fields["wrong_format_ids"]);
                setSnackbarMsg('Please correct the email format: ' + error_fields["wrong_format"].join(', '));
                setSnackbarOpen(true);
            } else {
                if (currentStep < 2) {
                    setCurrentStep(currentStep + 1);
                }
            }
        }
        else{
            if (currentStep < 2) {
                setCurrentStep(currentStep + 1);
            }
        }
    }


    const handleLoadClientFromAPI = () => {
        API.getClient(props.match.params.client_id).then(response => {
            console.log('get client info', response); 
            const data: any = Object.assign({}, response);

            const client = convertClientData(data);

            SetClientData(Object.assign({}, clientData, client));
            setClientLoaded(true);
        }).catch((error) => {
            setClientLoaded(true);
            console.log('getbenefitlist api error: ', error);
        });
    }

    // const handleLoadBaseNodesFromAPI = () => {
    //     setLoading(true);

    //     const GET_URL = 'http://18.138.94.224:4101/api/' + "base-benefit-plans/";
    //     // const GET_URL = API_URL + "base-benefit-plans/";

    //     return fetch(GET_URL, {
    //         method: 'get',
    //         headers: {'Content-Type':'application/json'}
    //     }).then(response => response.json()
    //     ).then((response: any) => {
    //         const data: any[] = [...response];

    //         const nodes = convertNodeData(data);
                
    //         handleLoadBenefitNodesFromAPI(nodes);
    //     }).catch(error => {
    //         setLoading(false);
    //         console.log('getbenefitlist api error: ', error);
    //     });
    // }

    // const handleLoadBenefitNodesFromAPI = (baseNodes: any[]) => {
    const handleLoadBenefitNodesFromAPI = () => {
        setLoading(true);
        const GET_URL = API_URL + "benefit-plans/tree/" + props.match.params.proposal_id;

        return fetch(GET_URL, {
            method: 'get',
            headers: {'Content-Type':'application/json'}
        }).then(response => response.json()
        ).then((response: any) => {
            const data: any[] = [...response];
            let nodes : any[] = [];
            let eList : any[] = [...exclusionList];

            nodes = convertNodeData(data, true);
            eList = createExclusionList(nodes);
            // const benefitNodes = convertNodeData(data, true);
                
            // let nodes: any[] = combineBaseBenefitNodes(baseNodes, benefitNodes);

            setNodesLoaded(true);
            SetExclusionList(eList)
            SetBenefitNodes (nodes);
        }).catch(error => {
            setNodesLoaded(true);
            console.log('getbenefitlist api error: ', error);
        });
    }


    function handleDownloadFile(){
        //API Call to download file
        // API.downloadProposal(id,body).then(response => {
            // if(response !== undefined){
                // let url = window.URL.createObjectURL(response);
                let a = document.createElement('a');
                a.href = 'proposal.pdf';
                a.download = 'proposal.pdf';
                a.click();
                //setPopupoverOpen(false);
        //     }
        // })
    }

    function handleDeleteFile(index:number){
    	let temp = Object.assign([],uploaded);
    	// delete temp[index];
    	temp.splice(index,1);
    	setUploaded(temp);
    }


    function handleFileUpload(acceptedFile: any){
    	console.log('acceptedFile',JSON.stringify(acceptedFile[0]));
    	let reader = new FileReader();
		let src: any = null;
		let typeArray;

		reader.onload = function(event) {
			acceptedFile.name = acceptedFile[0].name;
		    acceptedFile.sample = false;
		    acceptedFile.type = acceptedFile.name.toLowerCase().includes('.pdf') ? 'pdf' : 'img';
		    if(event !== null && event.target !== null){
		    	src = (acceptedFile.type !== 'pdf') ? event.target.result : null;
		    	if(acceptedFile.type === 'pdf'){
		    		// typeArray = new Uint8Array(this.result);
		    		src = event.target.result;
		    		console.log('typeArray', typeArray);
		    	}
		    } 
		    // console.log(event.target !== null,event.target.result)
		    console.log('src: ',src);
		    acceptedFile.src = src;
		    
		    console.log('uploaded',uploaded);
		    setUploaded([...uploaded, acceptedFile]);
		};

		if(acceptedFile[0].name.toLowerCase().includes('.pdf')){
			reader.readAsArrayBuffer(acceptedFile[0]);
		} else {
			reader.readAsDataURL(acceptedFile[0]);
		}
    }

    function submitForSetup() {
        let cpayload = getClientPayload(clientData);
        //console.log('client payload', cpayload);

        setLoading(true);

        API.patchClient(props.match.params.client_id, cpayload).then(cresponse => {
            //console.log('patchClient response: ', cresponse);
            if(cresponse && cresponse.ok){
                setLoading(false);

                if(isApprover){
                    if (clientData.businessData && clientData.businessData !== undefined && clientData.businessData.corporateAccountNo
                        && clientData.businessData.corporateAccountNo !== undefined) {
                        // Processmaker.put('cases/'+props.match.params.case_id+'/route-case','',{})
                        //     .then(response => {
                        //         console.log('routecase response: ',response);
                        //         setRedirectToDashboard(true);
                        //     });
                        if(clientData.businessData.registeredCorporateName){
                            setCloseAccountTitle(clientData.businessData.registeredCorporateName + "  is now an Intellicare Client");
                        }
                        setCloseAccountMessage(
                            <div>
                                Congratulations on successfully closing an account. You can view the official contract here
        
                                <div style={{textAlign:"center", fontSize: '20px', fontWeight: 700, marginBottom: "15px", marginTop: "15px"}}>
                                    {clientData.businessData.corporateAccountNo}
                                </div>
                            </div>
                        );
                        setCloseAccountOpen(false);
                        setConfirmCloseAccount(true);
                    } else {
                        setCloseAccountOpen(false);
                        setLoading(true);
                        fetch(`${API_URL}clients/${props.match.params.client_id}/corporate-account-no`, {
                            method: 'PATCH',
                            headers: { 'Content-Type': 'application/json' },
                            body: JSON.stringify({})
                        })
                        .then(response => response.json())
                        .then(response => {
                            if (response['_id']) {
                                if(response['corporate_account_no'] && response['corporate_account_no'] !== null){
                                    if(response['registered_name']){
                                        setCloseAccountTitle(response['registered_name'] + "  is now an Intellicare Client");
                                    }
                                    setCloseAccountMessage(
                                        <div>
                                            Congratulations on successfully closing an account. You can view the official contract here
        
                                            <div style={{textAlign:"center", fontSize: '20px', fontWeight: 700, marginBottom: "15px", marginTop: "15px"}}>
                                                {response['corporate_account_no']}
                                            </div>
                                        </div>
                                    );
                                    setLoading(false);
                                    setConfirmCloseAccount(true);
                                }
                            }else{
                                setLoading(false);
                            }
                        })
                        .catch(error => {
                            console.log(error);
                            setLoading(false);
                        });
                    }
                }else{
                    Processmaker.put('cases/'+props.match.params.case_id+'/route-case','',{})
                        .then(response => {
                            console.log('routecase response: ',response);
                            setRedirectToDashboard(true);
                    });
                }

            }else{
				setLoading(false);
				setSnackbarMsg('An error occurred while updating kyc information.');
				setSnackbarOpen(true);
            }
        }).catch((error) => {
            console.log('patchClient response error ', error);
            setLoading(false);
			setSnackbarMsg('An error occurred while updating kyc information.');
			setSnackbarOpen(true);
        });
        
        // let files: any[] = uploaded.map(file => file[0]);

        // API for File Upload
        // API.uploadSignedProposal(files)
        //     .then(response => {
        //         console.log('uploadSignedProposal response:',response);
        //     });

		// let startCaseData = {
		// 	pro_uid: '7483319725dae85ea897f26051447119',
		// 	tas_uid: '2494937605dae8613759d85048305887'
		// }
		// if(isApprover){

			
                
		// } else {
		// 	Processmaker.postWithToken('cases','',startCaseData)
		// 		.then(response => {
		// 			console.log('startCase response: ',response);

		// 			if(response !== undefined && response.app_uid !== undefined){
		// 				Processmaker.put('cases/'+response.app_uid+'/route-case','',{})
		// 					.then(response => {
		// 						console.log('routecase response: ',response);
		// 					});
		// 			}
		// 		});
		// }

    }

    function closeAccount(){
        Processmaker.put(`cases/${props.match.params.case_id}/route-case`,'',{})
            .then(response => {
                console.log('routecase response: ', response);
                setRedirectToDashboard(true);
        });
    }

    useEffect(() => {
        setLoading(true);
        handleLoadClientFromAPI();
        handleLoadBenefitNodesFromAPI();
        // handleLoadBaseNodesFromAPI();
    }, []);

    useEffect(() => {
        if (clientLoaded && nodesLoaded) {
            setLoading(false);
        }
    }, [clientLoaded, nodesLoaded]);

    useEffect(() => {
        if (!contractLoaded && contractNode) {
            const client = {
                principalData: {
                    numOfPrincipals: contractNode.principals_count ? contractNode.principals_count : 0,
                    malePrincipals: contractNode.male_principals_count ? contractNode.male_principals_count : 0,
                    femalePrincipals: contractNode.female_principals_count ? contractNode.female_principals_count : 0,
                    numOfDependents: contractNode.dependents_count ? contractNode.dependents_count : 0,
                    maleDependents: contractNode.male_dependents_count ? contractNode.male_dependents_count : 0,
                    femaleDependents: contractNode.female_dependents_count ? contractNode.female_dependents_count : 0,
                }
            };
            SetClientData(Object.assign({}, clientData, client));
            setContractLoaded(true);
        }
    }, [contractNode]);


    if(redirectToDashboard){
        return <Redirect to={{pathname: '/underwriting/'}}/>
    } else {
    	return (
			<div style={{padding: '32px 24px'}}>

                { loading ?
                    (<LoadingIndicator/>) : null
                }

                {/* {closeAccountOpen ? ( */}
                <Modal
                    style={{
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center'
                    }}
                    open={closeAccountOpen}
                    onClose={() => {
                        setCloseAccountOpen(false);
                    }}
                    closeAfterTransition>
                    <Fade in={closeAccountOpen}>
                        <div 
                            style={{
                                backgroundColor: '#FFFFFF',
                                padding: '2rem',
                                outline: 'none',
                                width: '25rem'
                            }}>
                            <div
                                style={{
                                    fontSize: '1rem',
                                    fontWeight: 600,
                                    marginBottom: '1rem'
                                }}>
                                Close Account
                            </div>
                            <div 
                                style={{
                                    fontSize: '13px',
                                    fontWeight: 400,
                                    marginBottom: '1rem'
                                }}>
                                Please ensure that the proposal reflects the final 
                                agreement with the client. Closing this account 
                                will generate the official contract for their plan.
                            </div>
                            <div style={{textAlign: 'center'}}>
                                <Button
                                    variant='contained'
                                    onClick={() => {
                                        setCloseAccountOpen(false);
                                    }}>
                                    Cancel
                                </Button>
                                <div style={{width: '1rem', display: 'inline-block'}} />
                                <Button
                                    variant='outlined'
                                    color='primary'
                                    onClick={() => {
                                        submitForSetup();
                                    }}>
                                    Close Account
                                </Button>
                            </div>
                        </div>
                    </Fade>
                </Modal>
                {/* ) : null} */}

                <Snackbar
					key="close"
					anchorOrigin={{
						horizontal: 'right',
						vertical: 'top',
					}}
					open={snackbarOpen}
					onClose={() => {
						setSnackbarOpen(false);
					}}
					autoHideDuration={5000}
				>
					<SnackbarContent
						style={{ backgroundColor: 'rgba(0, 0, 0, 0.75)' }}
						message={snackbarMsg}
						action={
							<IconButton
								onClick={() => {
									setSnackbarOpen(false);
								}}
							>
								<CloseIcon style={{ color: 'white' }} />
							</IconButton>
						}
					/>
				</Snackbar>

				{/* <AuthenticatedLayout> */}
					<Grid
	                    container
	                    >
	                    <Grid item xs={12}>
	                        <Grid item xs={12}>
	                            {/*<Components.UI.BreadcrumbsComponent items={breadcrumbs}/>*/}
	                        </Grid>

	                        <Grid item xs={12}>
	                            <PageHeaderComponent
	                                id={"finalize_proposal_stepper"}
	                                label={props.businessData ? props.businessData.registeredCorporateName : ''}
	                                steps={steps}
	                                activeStep={currentStep}
	                            />
	                        </Grid>
	                    </Grid>
	                </Grid>
                    {renderStep(currentStep)}
					<Grid
	                    container
	                    alignItems="stretch">
	                    <Grid item xs={12}>
	                        <MyFloatingButtons
	                            submitForSetup={() => {
                                    if(isApprover){
                                        setCloseAccountOpen(true);
                                    }else{
                                        submitForSetup();
                                    }
                                }}
	                            isApprover={isApprover}
                                currentStep={currentStep}
                                setStepBack={setStepBack}
                                setStepForward={setStepForward}
	                        />
	                    </Grid>
	                </Grid>
				{/* </AuthenticatedLayout> */}
                <ModalComponent
                    isModalOpen={confirmCloseAccount}
                    title={closeAccountTitle}
                    message={closeAccountMessage}
                    onClose={()=>{
                        closeAccount();
                    }}
                    buttons={
                        <div>
                            <Button
                                onClick={closeAccount}
                                className={buttonclasses.cancelButton}
                                variant="contained"
                                color="secondary"
                            >
                                Okay
                            </Button>
                        </div>
                    }
                />
			</div>
		);	
    }
	
};
const mapStateToProps = (state: Store) => {
    return {
        benefitNodes: getBenefitNodes(state),
        clientData: getClientData(state),
        contractNode: selectContractNode(state),
        categoryNode: selectCategoryNode(state),
        planTypeList: selectPlanTypeList(state),
        roomAndBoardList: selectRoomAndBoardList(state),
        coverageList: selectCoverageList(state),
        riderList: selectRiderList(state),
        rows: selectRows(state),
        selectedPlanType: getSelectedPlanType(state),
        selectedPlanPackage: getSelectedPlanPackage(state),
        selectedBenefitCategory: getSelectedBenefitCategory(state),
        businessData: selectBusinessData (state),
        natureData: selectNatureData (state),
        contactDetailsData: selectContactDetailsData (state),
        HMOData: selectHMOData (state),
        principalData: selectPrincipalData(state),
        exclusionList: getExclusionList(state),
    }
};


const mapDispatchToProps = (dispatch: Dispatch) => ({
    SetBenefitNodes: bindActionCreators(FinalizeProposalActions.SetBenefitNodes, dispatch),
    SetClientData: bindActionCreators(FinalizeProposalActions.SetClientData, dispatch),
    SetSelectedPlanType: bindActionCreators(FinalizeProposalActions.SetSelectedPlanType, dispatch),
    SetSelectedPlanPackage: bindActionCreators(FinalizeProposalActions.SetSelectedPlanPackage, dispatch),
    SetSelectedBenefitCategory: bindActionCreators(FinalizeProposalActions.SetSelectedBenefitCategory, dispatch),
    SetExclusionList: bindActionCreators(FinalizeProposalActions.SetExclusionList, dispatch),
});
export { mapStateToProps, mapDispatchToProps };
